<template>
  <div class="orderDetail mescroll">
    <div class="address flex">
      <img src="../../assets/img/icon/dizhi.png" class="icon" />
      <div class="flex-2">
        <div class="personalInfo">
          <span class="name">收货人：{{detail.userName2}}</span>
          <span class="tenlo">{{detail.userMobile}}</span>
        </div>
        <div class="addressInfo">{{detail.addrText}}</div>
      </div>
    </div>
    <div class="line"></div>
    <div class="goodsInfo">
      <div class="goodsInfo-top flex">
        <div class="flex-2 title">商品信息</div>
        <div class="status" :style="detail.orderStatus == 1 || detail.orderStatus == 2?'color:#eb5252':detail.orderStatus == 3?'color:#333':'color:#999'">{{$utils.getText(detail.orderStatus)}}</div>
      </div>
      <div class="list">
        <div class="item" v-for="(item,index) in detail.scEcomOrdersDetailList" :key="index">
          <div class="shopName">{{item.scEcomGoods?item.scEcomGoods.gname:''}}</div>
          <div class="num">×{{item.gorderCount}}</div>
          <div class="price">￥{{item.scEcomGoods?item.scEcomGoods.goutPrice.toFixed(2):'0.00'}}</div>
        </div>
      </div>
      <div class="goodsInfo-bottom">
        <div class="flex1" v-if="detail.remarks">
          <div class="flex-1">备注</div>
          <div class="flex-2">{{detail.remarks}}</div>
        </div>
        <div class="line1"></div>
        <div class="flex">
          <div class="flex-1">商品数量</div>
          <div class="flex-2">{{detail.shopCount}}</div>
        </div>
        <div class="flex">
          <div class="flex-1">商品合计</div>
          <div class="flex-2">￥{{detail.oorderMoney?detail.oorderMoney.toFixed(2):'0.00'}}</div>
        </div>
        <div class="flex">
          <div class="flex-1">实付款</div>
          <div class="flex-2 allPrice">
            ￥
            <span>{{detail.oorderMoneyShifu?detail.oorderMoneyShifu.toFixed(2):'0.00'}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="orderInfo" :style="detail.orderStatus == 2 ||detail.orderStatus  == 1?'padding-bottom:70px':''">
      <div class="title">订单信息</div>
      <div class="flex">
        <div class="flex-1">订单编号</div>
        <div class="flex-2">{{detail.orderno}}</div>
      </div>
      <div class="flex">
        <div class="flex-1">下单时间</div>
        <div class="flex-2">{{detail.createDate}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus != 1 && detail.orderStatus != 11">
        <div class="flex-1">支付方式</div>
        <div class="flex-2">{{detail.pay_type == 1?'余额':'微信'}}</div>
      </div>
      <div class="flex" v-if="detail.orderStatus != 1 && detail.orderStatus != 11">
        <div class="flex-1">支付时间</div>
        <div class="flex-2">{{detail.opayWdate}}</div>
      </div>
    </div>
    <div class="bottom" v-if="detail.orderStatus  == 2 || detail.orderStatus  == 1">
      <div class="btns flex">
        <!-- <div class="btn btnPay" v-if="detail.orderStatus  == 3" @click="handle(1)">确认收货</div> -->
        <div class="btn btnPay" v-if="detail.orderStatus  == 1" @click="handle(3)">去付款</div>
        <!-- <div class="btn" v-if="detail.orderStatus  == 5 " @click="handle(5)">删除订单</div> -->
        <!-- <div class="btn" v-if="detail.orderStatus  == 1" @click="handle(6)">取消订单</div> -->
        <img v-if="detail.orderStatus == 2" @click="showQrcode()" class="qrcode" src="../../assets/img/icon/qrcode.png" alt />
      </div>
    </div>
    <van-popup v-model="show">
      <div class="popup-mid" @click="show = false">
        <img :src="qrcode" alt />
      </div>
    </van-popup>
  </div>
</template>
<script>
import { Dialog, Popup } from "vant";
import { getOrderDetail1, getQrcode, weixinPay } from "@/api";
export default {
  components: {
    [Popup.name]: Popup
  },
  data() {
    return {
      detail: {},
      show: false,
      qrcode: ""
    };
  },
  methods: {
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getOrderDetail1({
        id: this.$route.query.id
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.detail = res.data;
          this.detail.addrText =
            this.detail.addr.area.treeNames.replace(/\//g, "") +
            this.detail.addr.addrAddr;
        } else {
          this.$toast(res.msg);
        }
      });
    },
    showQrcode(id) {
      getQrcode({
        qrurl:
          this.$config.http +
          "/wx/#/closeOrderDetail?type=1&id=" +
          this.detail.id +
          "&orderCode=" +
          this.detail.orderCode
      }).then(res => {
        if (res.code == 0) {
          this.qrcode = res.data.qrCode;
          this.show = true;
        }
      });
    },
    handle(type) {
      let that = this;
      switch (type) {
        case 1:
          //确认收货
          Dialog.confirm({
            title: "提示",
            message: "是否确定收货？"
          })
            .then(() => {
              // on confirm
              that.delivery();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 2:
          //查看物流
          this.toLogistics();
          break;
        case 3:
          //去付款
          Dialog.confirm({
            title: "提示",
            message: "是否确定付款？"
          })
            .then(() => {
              // on confirm
              that.makePreOrder();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 4:
          //联系客服
          break;
        case 5:
          //删除订单
          Dialog.confirm({
            title: "提示",
            message: "是否确定删除？"
          })
            .then(() => {
              // on confirm
              that.delete();
            })
            .catch(() => {
              // on cancel
            });
          break;
        case 6:
          //取消订单
          Dialog.confirm({
            title: "提示",
            message: "是否确定取消？"
          })
            .then(() => {
              // on confirm
              that.cancel();
            })
            .catch(() => {
              // on cancel
            });
          break;
        default:
          console.log(type);
      }
    },
    delivery() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      deliveryGoods({
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        order_id: this.detail.id
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("已收货");
            this.loadData();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "网络错误",
            type: "cancel"
          });
        });
    },
    delete() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      deleteOrder({
        token:
          sessionStorage.getItem("token") ||
          "177128e71cd2d4bc4acdba48bcae04709",
        order_id: this.detail.id
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("已删除");
            setTimeout(() => {
              this.$router.back();
            }, 500);
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "网络错误",
            type: "cancel"
          });
        });
    },
    cancel() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      cancelOrder({
        order_id: this.detail.id
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("已取消");
            this.loadData();
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          this.$vux.loading.hide();
          console.log(err);
          this.$vux.toast.show({
            text: "网络错误",
            type: "cancel"
          });
        });
    },
    makePreOrder() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      weixinPay({
        id: this.detail.id
      })
        .then(res => {
          loading.clear();
          if (res.code == 0) {
            if (!res.data.appId) {
              this.$toast("支付成功", "middle");
              this.loadData();
            } else {
              this.weChatPay(
                res.data.appId,
                res.data.timeStamp,
                res.data.nonceStr,
                res.data.packageValue,
                res.data.signType,
                res.data.sign
              );
            }
          } else {
            this.$toast(res.msg);
          }
        })
        .catch(err => {
          loading.clear();
          console.log(err);
          this.$toast({
            text: "网络错误",
            type: "cancel"
          });
        });
    },
    weChatPay(appId, timeStamp, nonceStr, packageValue, signType, sign) {
      console.log(appId, timeStamp, nonceStr, packageValue, signType, sign);
      var $this = this;
      if (typeof WeixinJSBridge == "undefined") {
        if (document.addEventListener) {
          document.addEventListener(
            "WeixinJSBridgeReady",
            onBridgeReady,
            false
          );
        } else if (document.attachEvent) {
          document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
          document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
        }
      } else {
        onBridgeReady();
      }
      function onBridgeReady() {
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest",
          {
            appId: appId, //公众号名称，由商户传入
            timeStamp: timeStamp, //时间戳，自1970年以来的秒数
            nonceStr: nonceStr, //随机串
            package: packageValue,
            signType: signType, //微信签名方式：
            paySign: sign //微信签名
          },
          function(res) {
            console.log(res);
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              $this.$toast("支付成功", "middle");
              $this.loadData();
            } else if (res.err_msg == "get_brand_wcpay_request:cancel") {
              $this.$toast("取消支付", "middle");
            } else if (res.err_msg == "get_brand_wcpay_request:fail") {
              $this.$toast("支付失败", "middle");
            }
            // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
          }
        );
      }
    }
  },
  mounted() {
    document.title = "订单详情";
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.orderDetail {
  .line {
    height: 5px;
    background: #efefef;
  }
  .line1 {
    border-top: 1pt solid #efefef;
    margin: 20px 0 5px;
  }
  .flex {
    display: flex;
    align-items: center;
    .flex-1 {
      flex: 1;
    }
    .flex-2 {
      flex: 2;
      position: relative;
      height: 100%;
    }
  }
  .address {
    padding: 15px;
    .icon {
      width: 20px;
      margin-right: 10px;
    }

    .flex-1 {
      flex: 1;
    }
    .flex-2 {
      flex: 2;
      font-size: 15px;
    }

    .personalInfo {
      font-size: 16px;
      line-height: 15px;
      font-weight: bold;
      .name {
        margin-right: 10px;
      }
      .tenlo {
        float: right;
      }
    }
    .addressInfo {
      font-size: 14px;
      color: #999;
      margin-top: 10px;
      line-height: 20px;
      .arrow {
        margin-top: 10px;
      }
    }
  }
  .goodsInfo {
    padding: 0 15px;
    margin-top: 10px;
    .goodsInfo-top {
      align-items: center;
      .title {
        font-size: 18px;
        font-weight: bold;
        line-height: 25px;
      }
      .status {
        font-size: 15px;
        line-height: 25px;
        color: $base-color;
      }
    }
    .list {
      .item {
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 20px;
        padding: 8px 0;
        .shopName {
          flex: 2;
        }
        .num {
          width: 30px;
          text-align: right;
          color: #999;
          margin-left: 10px;
        }
        .price {
          width: 70px;
          text-align: right;
          margin-left: 10px;
        }
      }
    }
    .goodsInfo-bottom {
      padding: 10px 0;
      .flex {
        padding: 5px 0;
        line-height: 25px;
        color: #999;
        font-size: 16px;
        display: flex;
        align-items: center;
        .flex-2 {
          text-align: right;
          color: #333;
          flex: 2;
        }
        .flex-1 {
          flex: 1;
        }
      }
      .flex1 {
        padding: 5px 0;
        line-height: 25px;
        color: #999;
        font-size: 16px;
        display: flex;
        .flex-2 {
          text-align: right;
          color: #333;
          flex: 2;
        }
        .flex-1 {
          flex: 1;
        }
      }
      .allPrice {
        text-align: right;
        font-size: 20px;
        font-weight: bold;
        span {
          font-size: 30px;
          font-weight: bold;
        }
      }
    }
  }
  .orderInfo {
    padding: 10px 15px 20px;
    .title {
      font-size: 18px;
      font-weight: bold;
      line-height: 25px;
      margin: 0 0 10px;
    }
    .flex {
      line-height: 30px;
      color: #999;
      font-size: 16px;
      display: flex;
      .flex-1 {
        flex: 1;
      }
      .flex-2 {
        text-align: right;
        color: #333;
        flex: 2;
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 50px;
    background: #fff;
    width: 100%;
    box-shadow: 0 -2px 10px 1px rgba(0, 0, 0, 0.1);
    padding: 5px 0;
    .btns {
      padding: 0 15px;
      justify-content: flex-end;
      margin-top: 9px;
      .btn {
        width: 100px;
        color: #666;
        border: 1pt solid #f4f4f4;
        float: right;
        height: 30px;
        line-height: 30px;
        border-radius: 5px;
        font-size: 13px;
        text-align: center;
        margin-left: 10px;
      }
      .btnPay {
        background: $base-color;
        color: #fff;
        border: 1pt solid $base-color;
      }
    }
  }
}
</style>
